:root {
  // Typography
  --nav-font: "Barlow Condensed", sans-serif;
  --title-font: "Bellefair", serif;
  --txt-font: "Barlow", sans-serif;

  --heading1: 80px;
  --heading1-tablet: 150px;

  --heading2: 56px;
  --heading2-tablet: 80px;
  --heading2-desktop: 100px;

  --heading3: 24px;
  --heading3-tablet: 40px;
  --heading3-desktop: 56px;

  --heading4: 16px;
  --heading4-tablet: 24px;
  --heading4-desktop: 32px;

  --heading5: 16px;
  --heading5-tablet: 20px;
  --heading5-desktop: 28px;

  --subheading1: 28px;

  --subheading2: 14px;
  --subheading2-tablet: 16px;

  --nav-txt: 16px;
  --nav-txt-tablet: 24px;
  --nav-txt-desktop: 32px;

  --body-txt: 15px;
  --body-txt-tablet: 16px;
  --body-txt-desktop: 18px;

  --btn-txt: 20px;
  --btn-txt-tablet: 32px;

  --heading5-letter-space: 2.7px;
  --heading5-letter-space-tablet: 3.38px;
  --heading5-letter-space-desktop: 4.75px;

  --subheading2-letter-space: 2.35px;
  --nav-letter-space: 2.36px;
  --nav-letter-space-tablet: 2.7px;
  --btn-letter-space: 1.25px;
  --btn-letter-space-tablet: 2px;

  // Colors
  --dark-grey: #0b0d17;
  --light-grey: #d0d6f9;
  --white: #ffffff;
  --semi-darkened-white: #7b7b7b;
  --darkened-white: #3f3f3f;

  // Sizes
  --outer-padding: 24px;
  --outer-padding-tablet-sides: 39px;
  --outer-padding-desktop-sides: 55px;
  --outer-padding-desktop-top: 40px;

  --header-height: 40px;
  --header-height-tablet: 48px;
  --main-title-padding: 16px;
  --main-title-padding-tablet: 24px;

  --nav-padding: 32px;
  --page-title-padding: 32px;
  --page-title-padding-tablet: 60px;

  --btn: 150px;
  --btn-tablet: 242px;
  --btn-desktop: 274px;
}

